body{
  font-family: 'Montserrat', sans-serif;
  overflow-x: hidden !important;
}
@media(max-width: 376px){
  .np{
    padding: .5rem !important;
}
}
  @media (min-width:377px) and (max-width:455px){
    .count{
      font-size: 22px;
      transition: all .4s ease-in-out;
      background-color: #343a4045 !important;
    }
  }
@media(max-width:767px){
  .dashboard-img{
    filter: blur(3px);
    -webkit-filter: blur(3px);
    transform: scale(2);
    margin-top: 30px;
    overflow: hidden !important;
  }
  .sub-heading{
    font-size: 22px;
    z-index: 9;
    margin-top: 25px;
    font-weight: 900;
    text-align: center;
    color: #fff;
    text-shadow: 1px 2px 11px black
  }
  .btn-rct{
    text-align: center;
  }
  .mt-100{
    margin-top: 100px;
  }
  .mx-4{
    margin-left: 0 !important;
    margin-right: 0 !important;
  }
  .sub-heading-1-main{
    font-size: 23px;
    font-weight: 900;
    line-height: 1.5;
  }
}
@media (min-width:992px) and (max-width:1376px){
}
.bg-transparent{
  background-color: #343a401a !important
}
@media (min-width: 768px) and (max-width: 991px){

  .count:hover{
    transition: all .3s ease-in-out;
    background-color: #343a4045 !important;
    cursor: pointer;
    transform: scale(1.1);
  }
  .sub-heading{
    font-size: 45px;
    z-index: 9;
    margin-top: 150px;
    font-weight: 900;
    text-align: center;
    color: #fff;
    text-shadow: 1px 2px 11px black
  }
  .btn-rct{
    position: absolute;
    text-align: center;
    bottom: 200px;
    z-index: 10;
  }
  .left-btn{
    right: 8px;
  }
  .right-btn{
    left: 25px;
  }
  .sub-heading-1-main{
    font-size: 33px;
    font-weight: 900;
    line-height: 1.5;
  }
}
@media(min-width: 768px){
